@import '../../styles/common.css';

.container {
  width: 100%;
  height: 100%;
  background-color: #fff;
}

.background,
.foreground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.background {
  height: 50%;
}

.bullet,
.heart,
.star {
  display: inline-block;
  font-size: 75%;
  margin-right: 0.25rem;
  vertical-align: middle;
}

.heart {
  margin-top: -0.1rem;
}

.star {
  margin-top: -0.25rem;
}

.companyName {
  cursor: pointer;
  outline: none;
}

.content {
  max-width: 1200px;
  width: 75%;
  display: flex;
  flex-direction: column;
  @media (max-height: 900px) {
    font-size: 90%;
    line-height: 1.75;
  }
  @media (max-width: 500px) and (max-height: 950px) {
    width: 100%;
    padding: 1rem;
    box-sizing: border-box;
  }
}

ul > li {
  @media (max-width: 500px) and (max-height: 950px) {
    border: 1px solid #000000;
    padding: 0.25rem 0.5rem;
    box-sizing: border-box;
    margin-bottom: 0.25rem;
  }
}

.foreground {
  z-index: 2;
  display: flex;
  flex: 1;
  height: 100%;
  justify-content: center;
  color: #000;
}

.parallax {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  @media (max-width: 500px) and (max-height: 950px) {
    margin-top: -50%;
  }
}

.header1 {
  @media (max-width: 450px) and (max-height: 950px) {
    font-size: 2rem;
  }
}

.header2 {
  @media (max-width: 450px) and (max-height: 950px) {
    font-size: 0.75rem;
    font-weight: bold;
    line-height: 1.5;
  }
}

.hireme {
  @media (max-width: 450px) {
    margin-top: 2rem;
  }
}
